.purple-btn {
  align-self: flex-start;
  padding: 22px 50px;
  font-weight: 700;
  font-size: 16px;
  line-height: 16px;
  color: var(--white-color);
  background: var(--purple-color);
  border: 2px solid transparent;
  border-radius: 10px;
  transition: all 0.3s ease;
}

.purple-btn:hover {
  background: #C998FB;
}

.purple-btn:focus-visible {
  background: #C998FB;
  border-color: #7033AC;
}

.purple-btn:active {
  background: #7033AC;
}

.btn-effects {
  background-color: transparent;
  border-radius: 16px;
  transition: all 0.3s ease;
}

.btn-effects svg path,
.btn-effects svg circle {
  transition: all 0.3s ease;
}

.btn-effects:hover svg path {
  stroke: var(--white-color);
}

.btn-effects:hover {
  background: var(--purple-color);
}

.btn-effects:focus-visible svg path {
  stroke: var(--white-color);
}

.btn-effects:focus-visible svg circle {
