}

.checkbox-common {
  position: relative;
  margin-top: -2px;
}

.input-checkbox {
  position: absolute;
  top: 0;
  left: 0;
  width: 14px;
  height: 14px;
  cursor: pointer;
  margin: 0;
  opacity: 0;
  z-index: 3;
}

.checkbox-common span {
  position: relative;
  padding-left: 22px;
  font-weight: 400;
  font-size: 14px;
  line-height: 19px;
  color: var(--black-color);
}

@media (max-width: 500px) {
  .checkbox-common span {
    font-size: 12px;
    line-height: 16px;
  }
}
.checkbox-common span::before {
  content: "";
  position: absolute;
  width: 14px;
  height: 14px;
  top: 2px;
  left: 0;
  border: 1px solid #999;
  border-radius: 2px;
  background-color: var(--purple-color);
  cursor: pointer;
}

.checkbox-common span::after {
  content: "";
  position: absolute;
  width: 8px;
  height: 6px;
  top: 5px;
  left: 3px;
  border-top: 2px solid var(--white-color);
  border-right: 2px solid var(--white-color);
  border-radius: 2px;
  transform: rotate(133deg);
  cursor: pointer;
  opacity: 0;
}

.input-checkbox:checked + span::after {
  opacity: 1;
}

.common-label {
  position: relative;
  font-weight: 400;
  font-size: 16px;
  line-height: 130%;
  color: #666;
  margin-bottom: 11px;
  cursor: pointer;
  width: max-content;
}

.common-input {
  position: absolute;
  top: 0;
  left: 0;
  cursor: pointer;
  width: 20px;
  height: 20px;
  z-index: 2;
  opacity: 0;
}

.common-label span {
  position: relative;
  padding-left: 32px;
}

.common-label span::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 20px;
  height: 20px;
  border: 1px solid #666;
  border-radius: 2px;
  transition: all 0.2s ease-in-out;
}

.common-label span::after {
  content: "";
  position: absolute;
  top: 2px;
  left: 6px;
  width: 8px;
  height: 13px;
  border-right: 2px solid var(--white-color);
  border-bottom: 2px solid var(--white-color);
  opacity: 0;
  transform: rotate(45deg);
  transition: all 0.2s ease-in-out;
}

.common-input:checked + span:before {
  background: #A65CF0;
}

.common-input:checked + span::after {
  opacity: 1;
}

.common-input:focus-visible + span::before {
  background-color: #EBD1FF;
}

.rules-link {
